/**
 * Round given number to given precision.
 * @param n 
 * @param precision 
 * @returns 
 */
export const round = (n: number, precision: number = 2): number => {
    const factor = 10 ** precision;

    return Math.round((n * factor)) / factor;
}

/**
 * Calculate percentage. Return zero if total is zero.
 * @param n 
 * @param total 
 * @returns 
 */
export const percent = (n: number, total: number): number => {
    if (total === 0) {
        return 0;
    }

    const pc = (n / total);

    return pc;
}

/**
 * Rounded percentage to given precision with `%` appended.
 * 
 * @param pc 
 * @param precision 
 * @returns 
 */
export const percentStr = (pc: number, precision: number = 2): string => {
    return `${round(pc * 100, precision)}%`;
}

/**
 * @from zigbee2mqtt-frontend
 */
export const toHex = (input: number, padding = 4): string => {
    const padStr = '0'.repeat(padding);
    return '0x' + (padStr + input.toString(16)).slice(-1 * padding).toUpperCase();
};
